#resume {
  width: 1200px;
  padding: 0 20px;
  margin: 10px auto;
  font-size: 16px;
  font-family:sourceHanSans-Bold;
  box-shadow: 1px 4px 10px 2px #000;
  background-color: #fff;
  .background {
    position: fixed;
    left: 0;
    top: 0;
    z-index: -10;
    width: 100vw;
    height: 100vh;
    background-color: #ccc;
  }
  .ps {
    padding-top: 10px;
    margin: 0 20px;
    font-size: 14px;
  }
}