#cut {
  width: 200px;
  height: 50px;
  margin: 10px 0;
  background-color: #805f7a;
  border-radius: 0 100px 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
  cursor: pointer;

  h2 {
    margin: 0;
    color: #f3fdff;
    font-weight: 600;
  }
}